.Testimonial {
      background-color: #f5fbf9;
  }
.Testimonial img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
  } 
.flex .client {
    margin: 0 35px;
    padding: 30px;
    height: 50vh;
    transition: all .3s ease-in-out;
  }
.flex .client .client-info {
  border-bottom: 2px solid #00d690;
  margin: 10px;
} 
.client-info .img {
  padding-bottom: 30px;
}
.client-info h5 {
  font-size: 18px;
}
.client-info h5, .client-info p{
  margin: 10px 15px 25px;
}
.client .rating ul li {
  margin: 10px 20px;
}
.client .rating ul li span {
  color: #e0a403;
}
@media screen and (max-width: 768px) {
  .Testimonial .slick-slide {
      width: 7% !important;
  }
}